import React, { Fragment } from "react";
import Navbar from "../../components/Navbar";
import Hero from "../../components/hero";
import Scrollbar from "../../components/scrollbar";
import Footer from "../../components/footer";
import PhotosWedding from "../../components/PortfolioSection2/photos_wedding";

const HomePage = () => {
  const listStyles = {
    listStyle: "disc",
    paddingLeft: "30px",
    marginBottom: "24px",
  };
  return (
    <Fragment>
      <Navbar hclass={"wpo-site-header-s1"} />
      <Hero />
      <div
        className="container"
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <h2>Takk for at du gjorde dagen s spesiell for oss!</h2>
        <p>
          Vi hadde en kjempefin dag, og det hper vi du ogs hadde. Under finner
          du bilder som fotografen tok.
        </p>
        <p>
          Dersom du nsker en eller flere av disse bildene i full strrelse, tak
          kontakt, s sender vi det til deg.
        </p>
        <div>
          Vi nsker fremdeles at du deler de bildene du selv tok.
          <ul style={listStyles}>
            <li>
              Opplasting her:{" "}
              <a target="_blank" href="https://mega.nz/megadrop/cYMiSFDoMpQ">
                https://mega.nz/megadrop/cYMiSFDoMpQ
              </a>{" "}
              (pner ny fane)
            </li>
            <li>
              Nedlasting av bilder dere har delt her:{" "}
              <a
                target="_blank"
                href="https://mega.nz/folder/lc9ymbgb#NWU1gYU5lPhgPs_G39qIhw"
              >
                https://mega.nz/folder/lc9ymbgb#NWU1gYU5lPhgPs_G39qIhw
              </a>{" "}
              (pner ny fane)
            </li>
          </ul>
        </div>
      </div>
      <PhotosWedding />
      <Footer />
      <Scrollbar />
    </Fragment>
  );
};

export default HomePage;
