const PhotosWedding = (props) => {
  return (
    <section
      className={`wpo-portfolio-section section-padding ${props.pClass}`}
      id="gallery"
    >
      <div className="container">
        <SectionTitle MainTitle={"Minner fra den store dagen"} />
        <p style={{ textAlign: "center", marginBottom: "40px" }}>
          "Foto: Gunnar Tjomlid / Tjomlid Foto"
        </p>
        <div className="wedding-images">
          {Portfolios.map((portfolio, idx) => (
            <img
              key={idx}
              title={portfolio.n}
              loading="lazy"
              src={portfolio.Pimg}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default PhotosWedding;
